import React, { useEffect, useState } from "react";

const resumeItems = [
  "Skills",
  "Education",
  "Projects",
  "Achievements",
  "Publications",
];

const Resume = () => {
  const [currSelected, setCurrSelected] = useState(0);
  const [currItem, setCurrItem] = useState(currSelected);
  const [currPos, setCurrPos] = useState({ top: 0, left: 0 });
  useEffect(() => {
    const coordParent = document
      .getElementById("tabs-container")
      .getBoundingClientRect();
    const coordChild = document
      .getElementById(resumeItems[currItem])
      .getBoundingClientRect();
    const box = document.getElementById("selected-box");
    setCurrPos({
      top: coordChild["top"] - coordParent["top"],
      left: coordChild["left"] - coordParent["left"],
    });
    box.style.transform = `translate(${currPos["left"]}px, ${currPos["top"]}px)`;
    box.style.height = coordChild.height;
    box.style.width = coordChild.width;
  }, [currItem, currPos]);
  useEffect(() => {
    setCurrItem(currSelected);
  }, [currSelected]);
  return (
    <section
      id="resume"
      className="flex snappedElement justify-center items-center py-16 sm:px-9  px-2 h-screen bg-gradient-to-bl from-green-50 to-blue-100"
    >
      {/*Resume Glass Container*/}
      <div className="grid grid-rows-6 sm:grid-rows-1 sm:grid-cols-10  w-full h-4/5 bg-gray-500 bg-opacity-5 ring-inset ring-2 ring-white rounded-3xl backdrop-blur max-w-3xl md:max-w-4xl shadow-lg overflow-hidden">
        <div className="flex flex-col justify-center mb-2 sm:mb-0 sm:pb-28 sm:col-start-1 sm:col-end-4 bg-gradient-to-b from-white to-blue-50 bg-opacity-80 sm:px-8 py-2 sm:py-12 overflow-hidden ">
          <div className="flex flex-col items-center w-full">
            <h2 className="text-xl sm:text-2xl w-full text-center text-gray-600 font-bold">
              Resume
            </h2>
            {/*Underline*/}
            <div className="border-purple-300 border-4 h-0 w-1/5 sm:w-1/2 rounded-lg" />
          </div>

          <div
            id="tabs-container"
            className="relative mt-2 sm:mt-5 flex justify-center sm:flex-col sm:justify-center flex-grow-0 flex-shrink-0 overflow-x-auto scrollbar-thin"
          >
            <div
              id="selected-box"
              className=" absolute top-0 left-0 bg-blue-100 bottom-0 w-1/6 h-3 sm:w-full h-8 z-0 rounded sm:rounded transition-all"
              style={{
                transform: `translate(${currPos["top"]}px, ${currPos["left"]}px)`,
              }}
            ></div>
            {resumeItems.map((item, index) => {
              return (
                <h3
                  className={`block rounded-sm cursor-pointer text-xs sm:text-base w-24 px-1 sm:mx-0 sm:w-full text-center py-1 cursor-pointer z-10 transition-all select-none ${
                    currSelected === index
                      ? `text-white bg-blue-${currItem !== index ? 300 : 400} `
                      : ""
                  }`}
                  key={index}
                  id={item}
                  onClick={() => {
                    setCurrSelected(index);
                  }}
                  onMouseEnter={() => setCurrItem(index)}
                  onMouseLeave={() => setCurrItem(currSelected)}
                >
                  {item}
                </h3>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
};
export default Resume;
