import "./App.css";
import Navbar from "./Nav/Nav";
import { HomeDark } from "./Home/Home";
import About from "./About/About";
import Resume from "./Resume/Resume";
import { Route, useLocation } from "react-router-dom";
import links from "./Nav/links";
import { useEffect } from "react";

function App() {
  const location = useLocation();

  useEffect(() => {
    const curr_id = links.find((item) => item.url === location.pathname).id;
    console.log(curr_id);
    const elementDOM = document.getElementById(curr_id);
    if (elementDOM) {
      elementDOM.scrollIntoView({
        behavior: "smooth",
        block: "center",
      });
    }
  }, [location]);
  return (
    <div
      className="h-screen overflow-y-auto overflow-x-hidden scrollbar scrollbar-thin scrollbar-track-white  scrollbar-thumb-gray-400 snapY"
      dir="ltr"
    >
      <Navbar />
      <HomeDark />
      <About />
      <Resume />
    </div>
  );
}

export default App;
